<template lang="">
    <div class="_full_router financial-detail-content">
       <custHeader :title="title"></custHeader>
      <div class="financial-detail-message">
          <h1 style="margin:6px 0;">{{financial.stkName}}</h1>
          <div class="financial-detail-box">
              <div style="">预期年化</div>
              <div>期限</div>
              <div>发行价格</div>
          </div>
          <div class="financial-detail-box" style="margin-top: 6px;">
              <div style="" class="red nhsy">{{financial.expectRate}}%</div>
              <div class="red fqx">{{financial.exiperDate}}天</div>
              <div class="red fqx">{{financial.issPrice | toMoney}}</div>
          </div>
          <mt-progress :value="jindu" :bar-height="2" :class="red"></mt-progress>
            <div class="financial-detail-box">
              <div>{{financial.subscribedLowAmt | toMoney}}元起</div>
              <div style="text-align:right;">剩余{{this.ktje | toMoney}}/{{financial.issAmt | toMoney}}</div>
          </div>
      </div>
   <!--    <div class="financial-detail-list" v-if=false>
          <mt-cell title="募集开始时间" :value="qsrq"></mt-cell>
          <mt-cell title="募集结束时间" :value="jzrq"></mt-cell>
          <mt-cell title="上市时间" :value="sssj"></mt-cell>
          <mt-cell title="终止时间" :value="zzsj"></mt-cell>
          <mt-cell title="商品类型" :value="prdType"></mt-cell>
          <mt-cell title="销售对象" :value="salesTarget" ></mt-cell>
          <input type="hidden" v-model="remoteOptions"/>
          <input type="hidden" v-model="remoteOptionsSalesTarget"/>
      </div> -->
       <div class="financial-detail-list">
        <div class="item-title">基本信息</div>
         <div class="normal-box financial-detail-item"> 
            <div class="left">发行状态</div>
          <div  class="right" v-remoteoptionsspan="dicts.issStatus">{{financial.issStatus}}</div>
         </div>
           <div class="normal-box financial-detail-item"> 
            <div class="left">管理状态</div>
            <div  class="right" v-remoteoptionsspan="dicts.stkMgrStatus">{{financial.stkMgrStatus}}</div>
         </div>
          <div class="normal-box financial-detail-item"> 
            <div class="left">风险级别</div>
            <div  class="right" v-remoteoptionsspan="dicts.riskLvl">{{financial.riskLvl}}</div>
         </div>
          <div class="normal-box financial-detail-item"> 
            <div class="left">货币代码</div>
            <div  class="right" v-remoteoptionsspan="dicts.currency">{{financial.currency}}</div>
         </div>

      </div>
       <div class="financial-detail-list">
        <div class="item-title">发行信息</div>
         <div class="normal-box financial-detail-item"> 
            <div class="left">发行时间</div>
          <div  class="right" >{{qsrq}}</div>
         </div>
           <div class="normal-box financial-detail-item"> 
            <div class="left">结束时间</div>
            <div  class="right" >{{jzrq}}</div>
         </div>
          <div class="normal-box financial-detail-item"> 
            <div class="left">发行规模</div>
            <div  class="right" >{{financial.issSize}}</div>
         </div>
          <div class="normal-box financial-detail-item"> 
            <div class="left">发行面值</div>
            <div  class="right" >{{financial.faceValue}}</div>
         </div>
         <div class="normal-box financial-detail-item"> 
            <div class="left">发行价格</div>
            <div  class="right" >{{financial.issPrice}}</div>
         </div>
          <div class="normal-box financial-detail-item"> 
            <div class="left">交易基数</div>
            <div  class="right" >{{financial.baseQty}}</div>
         </div>
         <div class="normal-box financial-detail-item"> 
            <div class="left">申购下限</div>
            <div  class="right" >{{financial.minLimitQty}}</div>
         </div>
          <div class="normal-box financial-detail-item"> 
            <div class="left">申购上限</div>
            <div  class="right" >{{financial.maxLimitQty}}</div>
         </div>
         <div class="normal-box financial-detail-item"> 
            <div class="left">资金申购数量</div>
            <div  class="right" >{{financial.fundAppQty}}</div>
         </div>
          <div class="normal-box financial-detail-item"> 
            <div class="left">认购交款数量</div>
            <div  class="right" >{{financial.subsribedAllotmentQty}}</div>
         </div>

      </div>
        <div class="financial-detail-list">
        <div class="item-title">当前状况</div>
         <div class="normal-box financial-detail-item"> 
            <div class="left">当前申购金额</div>
          <div  class="right">{{financial.applyAmt}}</div>
         </div>
           <div class="normal-box financial-detail-item"> 
            <div class="left">当前申购人数</div>
            <div  class="right" >{{financial.applyPersons}}</div>
         </div>
          <div class="normal-box financial-detail-item"> 
            <div class="left">当前关注人数</div>
            <div  class="right" >{{financial.concernPersons}}</div>
         </div>
          <div class="normal-box financial-detail-item"> 
            <div class="left">当前浏览次数</div>
            <div  class="right" >{{financial.hitCount}}</div>
         </div>

      </div>
      <div class="financial-detail-list">
        <div class="item-title">文书说明</div>
         <div class="normal-box financial-detail-item"> 
            <div class="left">风险说明</div>
          <div  class="right">{{financial.riskDesc}}</div>
         </div>
           <div class="normal-box financial-detail-item"> 
            <div class="left">风险揭示书</div>
            <div  class="right" >{{financial.riskFiles}}</div>
         </div>
          <div class="normal-box financial-detail-item"> 
            <div class="left">收费说明</div>
            <div  class="right" >{{financial.feeDesc}}</div>
         </div>
          <div class="normal-box financial-detail-item"> 
            <div class="left">收费说明书</div>
            <div  class="right" >{{financial.feeFiles}}</div>
         </div>
         <div class="normal-box financial-detail-item"> 
            <div class="left">产品说明</div>
            <div  class="right" >{{financial.prdDesc}}</div>
         </div>
          <div class="normal-box financial-detail-item"> 
            <div class="left">产品说明书</div>
            <div  class="right" >{{financial.prdFiles}}</div>
         </div>

      </div>
       <div class="app-footer">

            <div class="center" @click="goPay">
                    购买
            </div>
        </div>
    </div>
</template>
<script>
import cookieUtil from "js-cookie";
import { Navbar, TabItem, InfiniteScroll } from "mint-ui";
import { Toast } from "mint-ui";
import { Progress } from 'mint-ui';
import { Cell } from 'mint-ui';
import custHeader from  'components/custHeader'
Vue.component(Cell.name, Cell);
Vue.component(Progress.name, Progress);
Vue.use(InfiniteScroll);

export default {
  data() {
    return {
      dicts:{
        issStatus:{name:"ISS_STATUS",
          dataKey:"financial.issStatus"
      },
        stkMgrStatus:{name:"STK_MGR_STATUS",
        dataKey:"financial.stkMgrStatus"
      },
        riskLvl:{name:"RISK_LVL",
        dataKey:"financial.riskLvl"
      },
        currency:{name:"CURRENCY",
        dataKey:"financial.currency"
      },   
      },
      title:"详情",
      cacheList: {},
      financial:{id:"",stkName:"",
        "expectRate":"",
        "exiperDate":"",
        "subscribedAddAmt":"",
        "subscribedLowAmt":"",
        "issAmt":"",
        currency:"",
        salesTarget:'',
        "issBgnDate":"",
        issBgnTime:"",
        issPrice:"",
        baseQty:"",
        issEndDate:"",
        issEndTime:"",
        prdType:"",
        listDate:"",
        listTime:"",
        endDate:"",
        endTime:"",
        issSize:"",
        stkCode:'',
        faceValue:"",
        fundAppQty:'',
        applyAmt:"",
        applyPersons:"",
        subsribedAllotmentQty:"",
        concernPersons:"",
        hitCount:"",
        issStatus:"",
        stkMgrStatus:"",
        riskLvl:"",
        maxLimitQty:"",
        minLimitQty:"",

        riskDesc:"",
        riskFiles:"",
        feeDesc:"",
        feeFiles:"",
        prdDesc:"",
        prdFiles:"",

    },
        kgje:'',
        stkBiz:global.STKBIZ.SUBSCRIBE   ,   //资金申购  默认这个  不做认购申购
      prdType:"",
      salesTarget:""
    };
  },
   components:{
    custHeader
  },
  computed:{

      jindu:function(){
          return (this.financial.issAmt-this.ktje)/this.financial.issAmt*100==0?1:(this.financial.issAmt-this.ktje)/this.financial.issAmt*100;
      },
      ktje:function(){

      },
      sssj:function(){
          return dateFormatter(this.financial.listDate)+" "+timeFormatter(this.financial.listTime);
      },
      zzsj:function(){
          return dateFormatter(this.financial.endDate)+" "+timeFormatter(this.financial.endTime);
      },
      qsrq:function(){
          return dateFormatter(this.financial.issBgnDate)+" "+timeFormatter(this.financial.issBgnTime);
      },
      jzrq:function(){
           return dateFormatter(this.financial.issEndDate)+" "+timeFormatter(this.financial.issEndTime);
      },
      remoteOptions:function(){
          if(this.financial.prdType!=""){
              return dict("PRD_TYPE",this.financial.prdType).then(data=>{
                  this.prdType=data;
              });
          }

      },
      remoteOptionsSalesTarget:function(){
          if(this.financial.salesTarget!=""){
              return dict("SALES_TARGET",this.financial.salesTarget).then(data=>{
                  this.salesTarget=data;
              });
          }

      },

  },
  created: function() {
    // if (cookieUtil.get("userCode")) {
    //   console.log("yesy");
    // } else {
    //   console.log("no");
    //   this.$router.push("/login");
    // }
    let self=this;
    var par={issNo:this.$route.query.issNo,stkex:global.scdm};
    axiosUtil.upCltMallIssQry(par).then(function(data){
        var data=data.data;
        self.financial=data.rows[0];
        var item=self.financial;
        var issAmt = self.financial.issAmt?self.financial.issAmt:'0';//发行额度
            var applyAmt = self.financial.applyAmt?self.financial.applyAmt:'0';//已申购金额
            var fundAppQty=parseFloat(self.financial.fundAppQty?self.financial.fundAppQty:0);//资金申购数
            var subsribedAllotmentQty=parseFloat(self.financial.subsribedAllotmentQty?self.financial.subsribedAllotmentQty:0);//认购交款数
            var sy=0;
            if(self.stkBiz==global.STKBIZ.SUBSCRIBE){//如果是资金申购
                 sy=fundAppQty.subtr(parseFloat(applyAmt));//资金申购-已申购金额
            }else if(self.stkBiz==global.STKBIZ.SUBSCRIBE_PURCHASE){//认购申购
                 sy=subsribedAllotmentQty.subtr(parseFloat(applyAmt));//认购交款数-applyAmt
            }else if(self.stkBiz==global.STKBIZ.DECLARE_ISSUE || self.stkBiz==global.STKBIZ.MARKET_SELL || self.stkBiz==global.STKBIZ.PRIVATE_SELL){//如果配售
            //    var sy=query.quotaQty;//不做配售判断
            }
            self.kgje= sy;
    })
  },
  filters:{
      dateFormat:function(time){
         return dateFormatter(time);
     },
       toMoney:function(value){
            return toMoney(value);
       }
  },
  methods: {
     goBack:function(){
      this.$router.go(-1);
    },
    goPay:function(){
         this.$router.push("/pay?stkCode="+this.financial.stkCode+"&issPrice="+this.financial.issPrice+"&kgje="+this.kgje+"&stkBiz="+this.$route.query.stkBiz);
    }

  }
};
</script>
<style scoped>
.financial-detail-box {
  display: -webkit-flex; /* Safari */
  display: flex;
  font-size: 12px;
  color:#abb6ba;
}
.financial-detail-box > div {
  flex-grow: 1;
  width: 33.33%;
}
.nhsyl {
  font-size: 26px;
  color: #ef4f4f;
}
.nhsyl-text {
  font-size: 14px;
  color: #bbb;
}
.qx {
  margin-top: 12px;
}
.qx-text {
  font-size: 14px;
  color: #7089ff;
  border-right: 1px solid #ddd;
  padding: 0 4px;
  margin-right: 4px;
}
.app-content {
  height: 100%;
  background: #111;
}
.financial-content {
  height: 100%;
  background: #efeee9;
}
::-webkit-scrollbar {
  width: 0px;
  height: 1px;
}
::-webkit-scrollbar-thumb {
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
  background: rgba(0, 0, 0, 0.2);
}
.app-header{
        position: relative;
}
.financial-detail-content{
     background: #efeee9;
}
.financial-detail-message{
    background: #fff;
    padding: 12px;
}
.nhsy,.fqx{
    font-size: 18px;
}
.financial-detail-list{
    margin-top: 6px;
}

.financial-detail-item{
  background: #fff;
  padding: 12px;
  border-bottom: 1px solid #eee;
  font-size: 14px;
  color: #666;
}
.financial-detail-item .left{
  text-align: left;
}
.financial-detail-item .right{
  text-align: right;
}
.app-footer{
position: fixed;
}
.financial-detail-content{
  padding-bottom: 44px;
  overflow-y: auto;
}
.item-title{
      padding: 6px 12px;
    font-size: 12px;
    font-weight: 600;
}
</style>



